import react,{Component} from 'react'
import './Home.css'
import bg from '../image/Chrysanthemum.jpg'
import Card from './Card' 


class Home extends Component{

    constructor(props){

        super(props);

        this.state = {

            item1:{
              count:1,
              price:26
            
            },
            item2:{
              count:1,
              price:30
            },
            total:56
            
        }
    }
    

    minus = (id) =>{
      console.log(id);
      if(id == 1){
        let data = this.state.item1.count;
        let t = this.state.total;
        let price = this.state.item1.price;
        data = data - 1 ;
        if(data > 0){
          this.setState({item1:{count:data,price:price},total:t-price});
        }
      }else{
        let data = this.state.item2.count;
        let total = this.state.total;
        let price = this.state.item2.price;
        data = data - 1 ;
        if(data > 0){
          this.setState({item2:{count:data,price:price},total:total-price});
        }

      }
  
       
    }

    plus =(id) =>{

      console.log(id);
      if(id == 1){
        let data = this.state.item1.count;
        let t =this.state.total;
        let price = this.state.item1.price;
      

        data = data + 1 ;
        if(data > 0){
          this.setState({item1:{count:data,price:price},total:t+price});
        }
      }else{
        let data = this.state.item2.count;
        let total = this.state.total;
        let price = this.state.item2.price;
        data = data + 1 ;
        if(data > 0){
          this.setState({item2:{count:data,price:price},total:total+price});
        }

      }
    }

    render(){

        return(
            <div>
    <div class="basket">
      <div class="basket-labels">
         <h3>Shopping Cart</h3>
      </div>
      <div class="basket-product">
        <div class="item">
          <div class="product-image">
            <img src={bg} alt="Placholder Image 2" style={{borderRadius:"45%"}} className="product-frame"/>
          </div>
          <div class="product-details">
            <h1><strong><span class="item-quantity">4</span> x Eliza J</strong> Lace Sleeve Cuff Dress</h1>
          </div>
        </div>
        <div class="price" style={{float: "right"}}>30.00</div>
        <div class="number" style={{marginTop: "14px"}}>
        <span className="minus" onClick={this.minus.bind(this,1)} style={{marginLeft: "-309px"}}>-</span>
        <input type="text" value={this.state.item1.count} />
        <span className="plus"  onClick={this.plus.bind(this,1)}style={{marginLeft: "108px"}}>+</span>
      </div>
      </div>
      <div class="basket-product">
        <div class="item">
          <div class="product-image">
          <img src={bg} alt="Placholder Image 2" style={{borderRadius:"45%"}} className="product-frame"/>
          </div>
          <div class="product-details">
            <h1><strong><span class="item-quantity">1</span> x Whistles</strong> Amella Lace Midi Dress</h1>
          </div>
        </div>
        <div class="price" style={{float: "right"}}>26.00</div>
    
        <div class="number" style={{marginTop: "14px"}}>
        <span className="minus" onClick={this.minus.bind(this,2)} style={{marginLeft: "-309px"}}>-</span>
        <input type="text" value={this.state.item2.count} />
        <span className="plus"  onClick={this.plus.bind(this,2)}style={{marginLeft: "108px"}}>+</span>
      </div>
      </div>
    </div>
    <aside>
      <div class="summary">
        <div class="summary-total-items"><span class="total-items"></span> Items in your Bag</div>
        <div class="summary-subtotal">
          <div class="subtotal-title">Subtotal</div>
          <div class="subtotal-value final-value" id="basket-subtotal">{this.state.total}</div>
          <div class="summary-promo hide">
            <div class="promo-title">Promotion</div>
            <div class="promo-value final-value" id="basket-promo"></div>
          </div>
        </div>
        <div class="summary-delivery">
          <select name="delivery-collection" class="summary-delivery-selection">
              <option value="0" selected="selected">Select Collection or Delivery</option>
             <option value="collection">Collection</option>
             <option value="first-class">Royal Mail 1st Class</option>
             <option value="second-class">Royal Mail 2nd Class</option>
             <option value="signed-for">Royal Mail Special Delivery</option>
          </select>
        </div>
        <div class="summary-total">
          <div class="total-title">Total</div>
        <div class="total-value final-value" id="basket-total">{this.state.total}</div>
        </div>
        <div class="summary-checkout">
         
        </div>
      </div>
    </aside>
    </div>

        );
    }
}

export default Home;